html, body {
  height: 100%;
  width: 100%;
  font-size: 15px;
  overflow: hidden;
  position: fixed;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: inherit;
}

@media (min-width: 64em) {
  html {
    font-size: 15px;
  }
}

@media (min-width: 80em) {
  html {
    font-size: 16px;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root,
.App {
  width: 100%;
  height: 100%;
  background-color: #000;
  color: #fff;
}

.App {  
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  max-height: 100vh;
  overflow: hidden;
}

.App-Main {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
}

.App-Header {
  position: relative;
  background-color: rgba(0,0,0,.5);
}

.App-Play {
  flex: 1;
}

.App-Aside {
  position: relative;
  background-color: rgba(0,0,0,.5);
}

.App-Content {
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.App-Video {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.App-Footer {
  /* flex: 1; */
}

.Header-Content {
  flex: 1;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}

.Header-Logo {
  display: flex;
  padding: .625em;
  box-sizing: border-box;
}

.Header-Controls {
  display: flex;
}

.Logo {
  display: block;
}

.User_view_header {
  display: flex;
  align-items: center;
  padding: .625em;
  box-sizing: border-box;
}

.User-Pic {
  display: block;
  border-radius: 50%;
}

.User-Content {
  padding-left: .5em;
  padding-right: .5em;
  box-sizing: border-box;
}

.User-Heading {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.Heading {
  margin: 0;
  font-weight: bold;
}

.Heading_size_m {
  font-size: 1.25rem;
}

.Heading_size_s {
  font-size: 1rem;
}

.Button {
  display: inline-flex;
  align-content: center;
  align-items: center;
  padding: 0;
  background-color: transparent;
  border: none;
  border-width: 0;
  margin: 0;
  font-size: 1rem;
  transition: background-color 250ms;
  opacity: .7;
  color: inherit;
}

.Button:hover {
  opacity: 1;
}

.Button-Text {
  line-height: 1.75;
  padding: 1em .625em;
  text-align: center;
}

.Button_fluid .Button-Text {
  display: block;
  width: 100%;
}

.Button_fluid {
  flex: 1;
}

.Button_checked {
  opacity: 1;
  background-color: rgba(0,0,0,.5);
}

.Icon {
  display: inline-block;
  padding: .625em;
}

.Aside {
  display: flex;
  flex-wrap: nowrap
}

.Aside-Controls {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Aside-ControlsGroup {
  display: flex;
  flex-direction: column;
}

.Aside-Main {
  width: 20em;
  background-color: rgba(0,0,0,.5);
  /* display: none; */
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}

.Aside-Menu {
  display: flex;
  flex-wrap: nowrap;
  box-shadow: 0 1px 0 #666;
  overflow: hidden;
}

.Aside-Content {
  flex: 1;
  padding: 1em;
  box-sizing: border-box;
  word-wrap: break-word;
  overflow: auto;
  hyphens: auto;
}

.Footer {
  position: relative;
}

.Footer-Progress {
  position: relative;
  z-index: 1;
}

.Footer-Controls {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  position: relative;
  z-index: 1;
}

.Footer-ControlsGroup {
  display: inline-flex;
}

.Footer-Ruler {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;  
}

.Ruler {
  display: flex;
  flex-wrap: nowrap;
  height: 100%;
}

.Ruler-Chapter {
  flex: 1;
  height: 100%;
  background-color: #000;
}

.Ruler-Chapter + .Ruler-Chapter {
  box-shadow: inset 1px 0 0 #666;
}

.Video {
}

.Video-Inner {
  width: 100%;
  height: 100%;
}

.Video-Content {
  flex: 1;
}

.Video_view_preview {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
}

.Input_width_available {
  display: block;
  width: 100%;
}

.Paragraph {
  margin: 0;
}

.Paragraph + .Paragraph {
  margin-top: 1em;
}

.List {
  margin: 0;
  padding-left: 2em;
}

.List_type_unstyled {
  list-style: none;
  padding: 0;
  margin: 0;
}

.List_of_videos .List-Item {
  margin-top: 1em;
}

